//font-family: 'Playfair Display', serif;
//font-family: Source Sans Pro;
//font-family: 'Cuprum', sans-serif;
$color1: white;
$color2: #c59d5f;//gold for h1
$color3: #4f4f4f;//gray for 
$color4: #000a5f;//blue for a
$color5: #2c3d97;//blue for a:hover
$color6: #d51313;//red for a:focus in nav
body{
	background: white;
	font-family: Source Sans Pro;
}
img{
	max-width: 100%;
}
a{text-decoration: none!important;}
button:focus,a:focus,input:focus,select:focus{outline:none!important;}
// General style-----------------------------------------------
// header
header{
	background: white;
	height: 125px;
	color:$color4; 
	padding-top: 20px;
	// fix pos sett
	position: fixed;
	top: 0;
	z-index: 999;
	width: 100%;
	box-shadow: 0 2px 28px -2px rgba(0, 0, 0, 0.75);
	.pushy-content{
		text-align: center;
		li{
			list-style-type: none;
		}
	}
	.flex{
		display: flex;
		flex-direction: row;
		align-items: center ;
		justify-content: space-around;
	}
	// a{
	// 	width: 30%;
	// }
}
.desk{

}
nav{
	display: inline-block;
	margin-top: 25px;
	// margin-left: 20px;
	text-align: right;
	float: right;
	li{
		padding: 0 calc(3px + 0.3vw) !important;
	}
	a{
		color: $color4;
		font-size: 18px;
		font-family: 'Source Sans Pro';
		&:hover{
			color: $color3;
		}
		&:focus{
			color: $color6;
		}	
	}

}
@media screen and (max-width: 991px) {
nav {
       margin-top: 20px; 
    }
}
// Header END
//footer----------------------------------------
footer{
	min-height:  250px;
	background: white;
	text-align: center;
	padding-top: 70px;
	padding-bottom: 60px;
	border-top: 1px solid #808080;
	.bank{
		margin-bottom: 20px;
	}
	img{
		float: right;
		margin-right: 40px;
	}
	a{
		color: $color4;
		font-size: 16px;
		text-decoration:underline!important;
		&:hover{
			color: $color5;
		}
	}
	ul{
		text-align: left;
		li{
			margin-right: 7px;
		}
	}
	.col{
		display: inline-block;
		margin-right: 17px;
		margin-left: 17px;
		margin-bottom: 30px;
		p{
			margin-bottom: 0px;
			font-size: 16px;
		}
	}
	.telephone{
		text-decoration: none!important;
		color: $color3;
	}
}
// Исправление футера
@media screen and (max-width: 480px) {
.col {
        display: block!important;
		width: 100%;
		margin-right: 0!important;
		margin-left: 0!important;
    }
    footer{
		li{
			margin-right: 0px!important;
		}
	}
}
#fb{
    background: url('../img/mesenger_sprite.png') 0 0;
    width: 26px;
	height: 26px;
	&:hover{
		background: url('../img/mesenger_sprite.png') -26px 0;
	}
}
#tw{
    background: url('../img/mesenger_sprite.png') 0 -26px;
    width: 26px;
	height: 26px;
	&:hover{
		background: url('../img/mesenger_sprite.png') -26px -26px;
	}
}
#vk{
    background: url('../img/mesenger_sprite.png') 0 -52px;
    width: 26px;
	height: 26px;
	&:hover{
		background: url('../img/mesenger_sprite.png') -26px -52px;
	}
}
#ig{
    background: url('../img/mesenger_sprite.png') 0 -78px;
    width: 26px;
	height: 26px;
	&:hover{
		background: url('../img/mesenger_sprite.png') -26px -78px;
	}
}
// footer END
// General style END-------------------------------------------
// Banner
.banner{
	padding: 0!important;
	margin-top: 125px;
	// background: url("../img/BG_banner.jpg")center no-repeat;
	height: 215px;
	text-align: center;
	color: $color1;
	font-size: 16px;
	h1{
		color: $color1;
		font-size: 69px;
		font-family: 'Playfair Display';
		font-style: italic;
		margin-top: 50px;
		margin-bottom: 15px;	
	}
	a{
		color: $color1;
		&:hover{
			text-decoration: underline!important;
		}
	}
}
@media screen and (max-width: 610px) {
.banner {
	h1{

        font-size: 50px;
	}

    }
}
// Banner END

// Content
.content{
	background: white;
	padding-top: 60px;
	padding-bottom: 60px;
	font-size: 16px;
	line-height: 27px;
	color: $color3;
	.gold{
		color:$color2;
		font-size: 36px;
		font-family: Playfair Display, serif;
		font-style: italic;
		font-weight: normal;
		margin-top: 15px;
		margin-bottom: 20px;
		text-align: center;
	}
	.text_align{
		text-align: center;
		max-width: 770px;
		margin-right: auto;
		margin-left: auto; 
	}
	img{
		margin: 30px 0;
	}
	.container{
		max-width: 1000px;
	}
	h2{
		font-size: 32px;
		font-weight: bold;
	}
	h3{
		font-weight: bold;
	}
	a{
		color: $color4;
		text-decoration:underline!important;
		&:hover{
			color: $color5;
			text-decoration:none!important;
		}
	}
	ul{
		li{
			list-style-type: none;
			margin-bottom: 15px;
		}
		li:before {
			color: $color4;
			content: "♦";
			padding-right: 10px;
		} 
	}
	ol{
		li{
			color: $color4;
			font-weight: bold;
			margin-bottom: 15px;
			span{
				color: $color3;
				font-weight: 500;
				padding-left: 10px;
			}
		}
	}
	table{
		max-width: 785px;
		width: 100%;
		margin-top: 20px;
		th{
			background: $color4;
			color: $color1;
			min-height: 55px;
			padding: 13px 5px 13px 30px;
			border: 1px solid white;
		}
		tr{
			min-height: 55px;
		}
		td{
			border: 1px solid #eceff1;
			padding: 13px 5px 13px 30px;
			&:first-child{
				width:70%;
			}
		}
	}
}
// Pages navigation
.pages{
	text-align: center;
	a{
		padding: 15px 20px;
		border: 2px solid #ebebeb;
		color: $color3;
		&:focus{
			border: 2px solid $color4;
			color: $color1;
			background: $color4;
		}
		&:hover{
			border: 2px solid $color4;
		}
	}
	ul{
		margin-bottom: 70px;
		margin-top: 25px;
	}
}
// Content END
// mobile menu
@media (max-width: 959px) {
	.desk{
		display: none;
	}
	.logo{
	margin: 0 auto;
	width: 100%;
	}
	.logo1{
		padding-left: 40px;
		padding-right: 30px;
	}
}
@media (min-width: 960px) {
	.menu-btn{
		display: none;
	}
	header{
		.container{
			width: 930px;
		}
	}
}
@media (min-width: 992px) {
	header{
		.container{
			width: 970px;
		}
	}
}
@media (min-width: 1200px) {
	header{
		.container{
			width: 1170px;
		}
	}
}
// Burger
.menu-btn{
	background: transparent;
	border: none;
	img{
		height: 19px;
		width: 27px;
	}
}
#container{
	position: absolute;
	z-index: 99999;
	left: 10px;
	div{
		height: 16px;
		width: 23px;
		background: url("../img/menu_ico3.png");
	}
}
.is-active{
	div{
		background: url("../img/menu_ico22.png") center!important;
	}
}
// Animated
.animated{
	animation-duration: 1.1s !important;
}
